.container-flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.desk-mb-1 {
  margin-bottom: 0.4rem;
}

.desk-mb-2 {
  margin-bottom: 0.8rem;
}

.desk-mb-3 {
  margin-bottom: 1.2rem;
}

.desk-mb-4 {
  margin-bottom: 1.6rem;
}

.desk-mb-5 {
  margin-bottom: 2rem;
}

.p-1 {
  box-sizing: border-box;
  padding: 1%;
}

.p-2 {
  box-sizing: border-box;
  padding: 2%;
}

.p-5 {
  box-sizing: border-box;
  padding: 5%;
}

.p-8 {
  box-sizing: border-box;
  padding: 8%;
}

.p-10 {
  box-sizing: border-box;
  padding: 10%;
}

.p-15 {
  box-sizing: border-box;
  padding: 15%;
}

.bg-color-first {
  background-color: $first;
}

.bg-color-first-alt {
  background-color: $first-alt;
}

.bg-color-second {
  background-color: $second;
}

.bg-color-third {
  background-color: $third;
}

.bg-color-fourth {
  background-color: $fourth;
}

.br-16 {
  border-radius: 16px;
}

.text-color-primary {
  color: $text-color-primary;
}

.text-color-secondary {
  color: $text-color-secondary;
}

.text-color-attention {
  color: $text-color-attention;
}
