.contacts {
  text-decoration: none;

  .contacts-container {
    width: 80%;
    max-width: 80%;
    .contacts-cell {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }
}

.icon-container {
  color: $fourth;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  font-size: 5rem;
  transition: all 0.2s;
}

.contacts-cell:hover {
  .icon {
    color: $third;
  }
}

@media screen and (min-width: 640px) {
  .contacts {
    .contacts-container {
      width: 90%;
      max-width: 90%;
      .contacts__row {
        display: flex;
        justify-content: space-around;
        align-items: center;

        .contacts-cell {
          gap: 1rem;
        }
      }
    }
  }
}
