@import "./constants";
@import "./RESET";
@import "./custom";
@import "./menu";
@import "./home";
@import "./about";
@import "./skills";
@import "./study";
@import "./work";
@import "./contacts";

section {
  overflow: hidden;
  min-height: 100vh;
  width: 100vw;
  padding-top: 115px;
  padding-bottom: 115px;
  box-sizing: border-box;
  background-color: $section1;
}

section:nth-child(2n) {
  background-color: $section2;
}

.italic {
  font-style: italic;
}

@media screen and (max-width: 639px) {
  section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .card-picture {
    display: grid;
    justify-content: center;
    margin: 10px 0;
    min-width: 100px;
    min-height: 100px;
  }

  .card-text {
    display: grid;
    row-gap: 6px;
    margin: 10px 0;
    border-bottom: 2px solid black;
    .card-text__header {
      padding-left: 6px;
    }
    .card-text__text {
      padding-left: 10px;
    }
  }

  .icon {
    width: 12px;
    height: 12px;
  }
}

@media screen and (min-width: 640px) {
  .card-picture {
    display: grid;
    justify-content: center;
    margin: 10px 0;
  }
  .icon {
    width: 16px;
    height: 16px;
  }
}
