.home {
  overflow: hidden;
  height: 100svh;
}

.home__pic {
  border-radius: 10%;
  background-color: whitesmoke;
  object-fit: cover;
  object-position: center;
  width: 100%;
  box-shadow: 0 0 10px rgb(0, 0, 0, 1);
}

.home__my-photo {
  position: relative;
  margin: auto;
}

.home__name {
  margin-top: 1rem;
}

@media screen and (max-width: 639px) {
  .home {
    display: flex;
    flex-direction: column;
    justify-content: start;

    .home__my-photo {
      align-self: center;
      text-align: center;
      width: 80%;
    }
  }
}

@media screen and (min-width: 640px) {
  .home {
    display: flex;
    align-items: center;
    justify-content: space-around;
    justify-items: center;

    .home__my-photo {
      text-align: center;
      width: 60vh;
    }

    .home__about {
      flex-wrap: wrap;
      max-width: 40%;
      flex: 1;
    }

    .home__list {
      padding-left: 3rem;
      margin-top: 1rem;
    }

    .home__list li {
      margin: 0.3rem 0;
    }
  }
}
