.nav {
  background-color: $mobile-menu-background;
  height: $menu-height;
  position: fixed;
  width: 100%;
  z-index: 100;
  left: 0;
  top: 0;
  color: $menu-color-text;
  display: block;

  .hamburger-button {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: 3rem;
    margin: 4px;
    height: calc(#{$menu-height} - 8px);
    right: 0;
    background-color: $hamberger-button-background;

    .line {
      height: 4px;
      margin: 6% 0;
      width: 50%;
      background-color: $hamberger-line-background;
      border-radius: 10%;
    }
  }

  .nav__list {
    background-color: $mobile-menu-list-background;
    position: absolute;
    width: 100%;
    left: 100%;
    top: $menu-height;
    transition: left 0.2s;
    list-style: none;

    li {
      background-color: $mobile-menu-list-li-background;
      text-align: end;
      padding: 0.4rem;
      margin: 2px 0;
      font-size: 1.5rem;
      cursor: pointer;
    }
  }
}

@media screen and (min-width: 640px) {
  .nav {
    height: 10%;
    overflow: hidden;
    .nav__list {
      position: static;
      width: 100%;
      height: 100%;
      background-color: unset;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;

      li {
        margin: 0 10px;
        padding: 6px;
        display: inline-block;
      }

      li::after {
        content: "";
        height: 2px;
        width: 100%;
        display: block;
        background-color: $menu-desktop-underline;
        transition: transform 0.6s;
        transform: scale(0, 1);
      }

      // Подсветка пунктов меню при наведении
      // li:hover {
      //   background-color: $menu-hover-background;
      //   color: $menu-hover-color;
      //   transition: background-color 0.6s;
      // }

      // li:hover:after {
      //   background-color: $menu-hover-color;
      // }

      .active::after {
        transform: scale(1, 1) !important;
      }
    }

    .hamburger-button {
      display: none;
    }
  }
}
