  border-radius: 10%;
  width: 50%;
  height: 4px;
  margin: 6% 0;
}

.nav .nav__list {
  background-color: #edf2f7;
  width: 100%;
  list-style: none;
  transition: left .2s;
  position: absolute;
  top: 50px;
  left: 100%;
}

