  text-align: end;
  cursor: pointer;
  background-color: #243447;
  margin: 2px 0;
  padding: .4rem;
  font-size: 1.5rem;
}

@media screen and (width >= 640px) {
  .nav {
    height: 10%;
    overflow: hidden;
  }

  .nav .nav__list {
    background-color: unset;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    display: flex;
    position: static;
  }

  .nav .nav__list li {
    margin: 0 10px;
    padding: 6px;
    display: inline-block;
  }

  .nav .nav__list li:after {
    content: "";
    background-color: #edf2f7;
    width: 100%;
    height: 2px;
    transition: transform .6s;
    display: block;
    transform: scale(0, 1);
  }

  .nav .nav__list .active:after {
    transform: scale(1) !important;
  }

  .nav .hamburger-button {
    display: none;
  }
}

