.skills {
  ul,
  li,
  h1,
  h2,
  h3 {
    display: inline-block;
  }

  .skills__container {
    max-width: 80%;

    .skills__row {
      ul {
        display: flex;
        justify-content: start;
        align-items: center;
        flex-wrap: wrap;
        gap: 0.4rem 0.8rem;

        li {
          border: 3px solid $fourth;
          border-radius: 15px;
          padding: 2px 8px;
          // background-color: lightblue;
          cursor: default;
        }
      }
    }
  }
}

@media screen and (min-width: 640px) {
  .skills {
    .skills__container {
      max-width: 60%;
    }
  }
}
